.SearchBar input {
    border-radius: 20px;
    color: #242a3d;
    padding-left: 40px;
    margin-bottom: 16px;
    background-color: rgb(255, 255, 255);
    border: 4px double rgba(116, 0, 0, 0.153);
  }
  
  .SearchIcon {
    position: absolute;
    font-size: 1.2rem;
    padding: 9px;
    padding-left: 15px;
    color: rgb(5, 57, 0);
  }
  
  .SearchIcon:hover {
    cursor: default;
    color: #cfced3;
  }